  created () {

                let fecha=new Date();
                fecha.toLocaleString('en-US', { timeZone: 'America/Managua'});
                let dia_hoy=fecha.getDate();
                if(dia_hoy<10)dia_hoy='0'+dia_hoy;
                let mes=fecha.getMonth()+1;
                if(mes<10)mes='0'+mes;
                if(dia_hoy=='00'){dia_hoy='01';}

                this.fecha_fin =fecha.getFullYear()+'-'+mes+'-'+dia_hoy;

                this.fecha_inicio=this.CalcularFechasemanaPasada();

                this.listarTickets();


},
 computed:{
              ...mapState(['usuario', 'token']),
 },

 methods:{

      ...mapMutations(['mostrarLoading','ocultarLoading']),


      listarTickets(){

            this.ticketsArray=[];
            this.totalCerrados=0;
            this.totalAbiertos=0;
            this.porcentajeAtencion=100;
            this.totalSolucionados=0;
            this.totalIndisponibles=0;
            this.totalRevisiones=0;

            this.tiempoMaxEspera=0;
            this.tiempoMinEspera='';
            this.tiempoPromedioEspera=0;
            this.totalHorasEsperaTecnico=0;


             this.totalSlaCumplido=0;
             this.totalSlaNOCumplido=0;
             this.porcentajeSlaCumplido=0;

             this.arrayNoAplicaSLA=[];

             let config={headers:{token:this.token}};
             this.mostrarLoading({titulo:'accediendo a datos'});
             let me=this;

             let Envio_comandos=0;
             let Revision_fisica=0;
             let Otra=0;


            axios.post('tickets-fechas-all',
              {
                'desde':new Date(this.fecha_inicio).toISOString().substr(0, 10),
                'hasta':new Date(this.fecha_fin).toISOString().substr(0, 10) 
              },
              config
              ).then(function(response){

              let respuesta=response.data;

               me.ticketsArray=respuesta;

               let total_horas=0;
              
              respuesta.map(function(x){

                if((x.horas_espera_tecnico) &&(parseFloat(x.horas_espera_tecnico)>0)){
                  me.totalHorasEsperaTecnico= me.totalHorasEsperaTecnico+parseFloat(x.horas_espera_tecnico);
                }

                  if(x.status==0){
                    me.totalAbiertos++;
                  }else{
                    me.totalCerrados++;
                  }

                   if(x.status==2){  //cacnelado por indisponibildiad de vehiculos
                    me.totalIndisponibles++;
                  }

                  if(x.status==1){
                    me.totalSolucionados++;

                    if(x.accion_correcion=='Revision_fisica'){
                        me.totalRevisiones++;
                        Revision_fisica++;
                    }

                    if(x.accion_correcion=='Envio_comandos'){
                      Envio_comandos++;
                    }

                    if(x.accion_correcion=='Otra'){
                      Otra++;
                    }

                  }


                  //si esta abierto verificar el tiempo que lleva abierto
                  if((x.status==0)||(!x.horas_espera_ticket)){  

                     let dateA = moment(x.fecha_reportado);
                     let dateB = moment(new Date());

                      let diferenciaMILs=dateB.diff(dateA);
                      diferenciaMILs=diferenciaMILs/1000;
                      diferenciaMILs=diferenciaMILs/3600;

                      if(parseFloat(diferenciaMILs)>0){
                                x.horas_espera_ticket=parseFloat(diferenciaMILs).toFixed(2);
                      }

                  }

                  total_horas=total_horas + parseFloat(x.horas_espera_ticket);


                  if(parseFloat(x.horas_espera_ticket)>0){
                      if(parseFloat(x.horas_espera_ticket) > me.tiempoMaxEspera){
                        me.tiempoMaxEspera= parseFloat(x.horas_espera_ticket);

                        me.itemMax=x;
                      }

                       if(me.tiempoMinEspera==''){
                               me.tiempoMinEspera=parseFloat(x.horas_espera_ticket);
                        }else{
                            if(parseFloat(x.horas_espera_ticket) < me.tiempoMinEspera){
                                me.tiempoMinEspera=parseFloat(x.horas_espera_ticket);
                            }
                        }
                  }


                  if(parseFloat(x.horas_espera_ticket)>16){
                    me.totalSlaNOCumplido++;
                    me.arrayNoAplicaSLA.push(x);
                  }else{
                    me.totalSlaCumplido++;
                  }

                 


                   

                 });

                  me.porcentajeAtencion= me.totalCerrados/parseInt(me.ticketsArray.length);
                  me.porcentajeAtencion= me.porcentajeAtencion * 100;
                  me.porcentajeAtencion=parseFloat(me.porcentajeAtencion).toFixed(2);


                  me.totalHorasEsperaTecnico=parseFloat(me.totalHorasEsperaTecnico).toFixed(2);

                  me.tiempoPromedioEspera=total_horas/me.ticketsArray.length;
                  me.tiempoPromedioEspera=parseFloat(me.tiempoPromedioEspera).toFixed(2);

                  
                   me.porcentajeSlaCumplido=me.totalSlaCumplido/parseInt(me.ticketsArray.length);
                   me.porcentajeSlaCumplido= me.porcentajeSlaCumplido * 100;
                   me.porcentajeSlaCumplido=parseFloat(me.porcentajeSlaCumplido).toFixed(2);


                  
            //grafica acciones realizadas 
           
              me.chartOptionsAcciones={   
                                  chart: {
                                    
                                      type: 'polarArea',
                                      height: 190,
                                      background: '#ffffff',
                                      foreColor: '#30043F',
                                      toolbar: {
                                        show: false
                                        }
                                    },
                                     stroke: {
                                        colors: ['#fff']
                                      },
                                       fill: {
                                        opacity: 1
                                      },
                                      yaxis: {
                                         show: false
                                          },
                                       legend: {
                                            position: 'right'
                                          },
                                     dataLabels: {
                                        enabled: false
                                      },
                                      labels:['Envio_comandos','Revision_fisica','Otra'],
                                     colors: ['#7DB9B6', '#4D455D','#B99B6B', '#F9DFDC', '#F9DFDC','#F9DFDC', '#F9DFDC', '#F9DFDC' ],
                                      dataLabels: {
                                       enabled: true
                                       } 

                                   
                                 };


                  me.chartDataAcciones=[Envio_comandos,Revision_fisica,Otra];


                me.listarTipoTicket();


               

                
                  me.dialogInicio=false;
                  me.ocultarLoading();
              }).catch(function(error){
                console.log(error);
              });

      },

      listarTipoTicket(){

        


            let config={headers:{token:this.token}};
            this.mostrarLoading({titulo:'accediendo a datos'});
            let me=this;

            let labels=[];
            let data_values=[];

            axios.get('tipotickets', config).then(function(response){

              console.log(response.data);
              
                let respuesta=response.data;

                  respuesta.map(function(x){
                        let total=0;
                        labels.push(x.nombre_tipo_ticket);
                        console.log(x.nombre_tipo_ticket);

                          me.ticketsArray.map(function(y){
                                if(y.id_tipo_ticket._id==x._id){
                                  total++;
                                }
                          });

                    data_values.push(total);

                   });





                        //llenamos pastel de tipo de tickets recibidos

                   me.chartOptions={
                                    chart: {
                                    width: 335,
                                    type: 'pie',
                                  },
                                  labels: labels,
                               
                            };
                     me.series=data_values;


                     me.listarEtiquetas();

                me.ocultarLoading();
            }).catch(function(error){
              console.log(error);
            });


      },

      listarEtiquetas(){


         let labels=[];
         let data_values=[];

         let config={headers:{token:this.token}};
         this.mostrarLoading({titulo:'accediendo a datos'});
          let me=this;

          axios.get('etiquetas', config
          ).then(function(response){

         let respuesta=response.data;
             respuesta.map(function(x){ 
                         let total=0;
                        labels.push(x.nombre_etiqueta);

                   me.ticketsArray.map(function(y){

                              if((y.id_etiqueta) &&(y.id_etiqueta._id==x._id)){
                                  total++;
                                }

                   });

                data_values.push(total);

              });//fin de for de etiquetas

        me.chartOptionsEtiquetas={
              chart: {
              type: 'bar',
              height: 350
            },
            plotOptions: {
              bar: {
                horizontal: true,
                columnWidth: '55%',
                endingShape: 'rounded'
              },
            },
            dataLabels: {
              enabled: true
            },
            stroke: {
              show: true,
              width: 2,
              colors: ['transparent']
            },
             xaxis: {
              categories: labels,
            },
            fill: {
              opacity: 1
            },
            tooltip: {
              y: {
                formatter: function (val) {
                  return " " + val + " "
                }
              }
            }
             };




             me.seriesEtiquetas=[
                              {
                               name: 'Cantidad',
                               data: data_values
                               }
                            ];

              me.listarTipoTrabajos();
                
            
              me.ocultarLoading();
          }).catch(function(error){
            console.log(error);
          });

      },

      listarTipoTrabajos(){


         let labels=[];
         let data_values=[];

         let config={headers:{token:this.token}};
         this.mostrarLoading({titulo:'accediendo a datos'});
          let me=this;

          axios.get('trabajos', config
          ).then(function(response){
             
               let respuesta=response.data;
                 respuesta.map(function(x){
                       let total=0;
                       labels.push(x.nombre_trabajo);

                         me.ticketsArray.map(function(y){

                              if((y.id_tipo_trabajo) &&(y.id_tipo_trabajo._id==x._id)){
                                  total++;
                                }

                       });


                         data_values.push(total);

                  });


            me.chartOptionsTrabajos={
              chart: {
              type: 'bar',
              height: 350
            },
            plotOptions: {
              bar: {
                horizontal: false,
                columnWidth: '55%',
                endingShape: 'rounded'
              },
            },
            dataLabels: {
              enabled: false
            },
             stroke: {
              show: true,
              width: 2,
              colors: ['transparent']
            },
             xaxis: {
              categories: labels,
             }
            };

            me.seriesTrabajos=[{ name:'total', data: data_values}];










              me.ocultarLoading();
          }).catch(function(error){
            console.log(error);
          });





      },





      CalcularFechasemanaPasada(){
          var fecha=new Date((new Date()).valueOf() - 1000*60*60*24*6);
          var mes=fecha.getMonth()+1;
          if(mes<10)mes='0'+mes;
          var dia=fecha.getDate();
          if(dia<10)dia='0'+dia;
          var textofecha=fecha.getFullYear()+'-'+mes+'-'+dia;

          return(textofecha);
      },

       IterarFechas(desde, hasta){

          //console.log('iteramos:'+desde+', hasta:'+hasta);
          
            let ArrayFechas_return=[];
          
            if(new Date(desde).toLocaleDateString()==new Date(hasta).toLocaleDateString()	){
                ArrayFechas_return.push(desde);
          
                console.log('mismo dia');
            }else{
          
          
                ArrayFechas_return.push(desde);

          
          
                let procesar=true;
                let horas=24;
              
                while(procesar){
              
                let ayer=new Date(desde.getTime() + horas*60*60*1000);
                var mes=ayer.getMonth()+1;
                if(mes<10)mes='0'+mes;
                var dia=ayer.getDate();
                if(dia<10)dia='0'+dia;
                var textofecha=ayer.getFullYear()+'-'+mes+'-'+dia;
                
                  
                  if(hasta.toISOString().slice(0,10)==new Date(textofecha).toISOString().slice(0,10)){
                    procesar=false;
                  }else{
                      //validamos hasta ayer-- hoy no debe ser validado
                    if(new Date(textofecha).toISOString().slice(0,10)==new Date().toISOString().slice(0,10)){
                    procesar=false;
                    }else{
                    ArrayFechas_return.push(new Date(textofecha+' 00:00:00'));
                    // console.log('aadimos:'+new Date(textofecha).toISOString().slice(0,10));
                    }
              
                  
              
                  }
                
                
                horas=horas+24
                }//finde procesar

                ArrayFechas_return.push(hasta);
          
          
            }//fin si hay varios dias
          
          
            return (ArrayFechas_return);
          
          
      },


       getDate(evaluar) {
          let date = new Date(evaluar),
          year = date.getFullYear(),
          month = (date.getMonth() + 1).toString(),
          formatedMonth = (month.length === 1) ? ("0" + month) : month,
          day = date.getDate().toString(),
          formatedDay = (day.length === 1) ? ("0" + day) : day,
          hour = date.getHours().toString(),
          formatedHour = (hour.length === 1) ? ("0" + hour) : hour,
          minute = date.getMinutes().toString(),
          formatedMinute = (minute.length === 1) ? ("0" + minute) : minute,
          second = date.getSeconds().toString(),
           formatedSecond = (second.length === 1) ? ("0" + second) : second;
          return formatedDay + "-" + formatedMonth + "-" + year  +' '+formatedHour+':'+formatedMinute+':'+formatedSecond;
        },




 }




